<div class="category_wrapper" :class="category.color"><div class="header"><router-link class="preview_icon bg_color" :to="`/categories/${category.id}`"><svgWrapper class="icon fill_color" :icon="category.icon"></svgWrapper></router-link><div class="favourite_icon" @click.prevent="toggleFavourite()" :class="{empty: category.is_favourite === false}"><svgWrapper class="icon" v-if="category.is_favourite" icon="favourite"></svgWrapper><svgWrapper class="icon" v-else icon="favouriteEmpty"></svgWrapper></div></div><div class="footer"><div class="data_wrapper"><router-link class="title" :to="`/categories/${category.id}`">{{ category.title }}</router-link><span class="done" v-if="todosLength &gt; 0 &amp;&amp; todosLength &gt; completedTodosLength">{{ completedTodosLength }}/{{ todosLength }} done</span><span class="done" v-else-if="todosLength &gt; 0 &amp;&amp; todosLength === completedTodosLength">All done!</span><span class="done" v-else-if="todosLength === 0">Empty :(</span></div></div></div>